@charset "UTF-8";
/* 移动端全屏 */
.pad-full-screen {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  z-index: 9999999; }

/* 移动端全屏 */
.pad-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  border: solid 1px #cdcdcd;
  box-sizing: border-box;
  user-select: none;
  overflow: hidden; }

.pad-wrap ul,
.pad-wrap li {
  list-style: none;
  padding: 0;
  margin: 0; }

.pad-hide {
  visibility: hidden; }

/* 分页 */
.pad-wrap .split-page-wrap {
  position: absolute;
  bottom: 10px;
  z-index: 100;
  line-height: 30px; }

.pad-wrap .split-page-wrap a {
  text-decoration: none;
  color: #444;
  width: 30px;
  height: 30px;
  border: solid 1px #cdcdcd;
  border-radius: 5px;
  font-size: 20px;
  display: inline-block;
  text-align: center;
  background: rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 5px 2px #cdcdcd inset;
  vertical-align: bottom; }

.pad-wrap .split-page-wrap a:hover {
  text-decoration: none !important;
  background: #cdcdcd; }

.pad-wrap .split-page-wrap span {
  margin-left: 15px;
  display: inline-block; }

.pad-wrap .split-page-wrap .go-page-btn {
  margin-left: 10px;
  font-size: 14px; }

.pad-wrap .split-page-wrap .page-number-input {
  width: 30px;
  text-align: center;
  line-height: 27px;
  height: 28px;
  padding: 0;
  margin: 0;
  outline: none;
  display: inline-block;
  border-radius: 3px;
  border: solid #cdcdcd 1px;
  background: rgba(0, 0, 0, 0.1); }

.pad-wrap .split-page-wrap .no {
  color: #cdcdcd !important;
  cursor: default; }

.pad-wrap .split-page-wrap .no:hover {
  background: rgba(0, 0, 0, 0.1) !important; }

/* 分页 */
/* toolbar横向布局和纵向布局的样式，外部可以覆写 */
.pad-wrap .toolbar-wrap {
  position: absolute;
  width: 100%;
  height: 38px;
  z-index: 10; }

.pad-wrap.vertical .toolbar-wrap {
  width: 40px;
  height: 100%; }

.pad-wrap.vertical .toolbar-wrap .toolbar-list .toolbar-item {
  position: relative; }

/* toolbar横向布局和纵向布局的样式，外部可以覆写 */
.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item {
  float: left;
  width: 40px;
  text-align: center;
  line-height: 36px;
  box-sizing: border-box;
  margin-left: 3px;
  cursor: pointer; }

.pad-wrap.vertical .toolbar-wrap .toolbar-list .toolbar-item {
  margin-left: 0; }

.vertical .can-wrap-outer {
  height: calc(62%) !important;
  height: calc(100% - 38px) !important;
  width: calc(60%) !important;
  width: calc(100% - 40px) !important;
  top: 0 !important; }

.left-top.vertical .can-wrap-outer,
.left-top.vertical .pad-tab-wrap {
  left: 40px; }

.vertical .pad-tab-wrap {
  width: calc(60%) !important;
  width: calc(100% - 40px) !important; }

.pad-wrap .toolbar-wrap .toolbar-list {
  width: 100%;
  height: 100%;
  background: #ececec; }

.pad-wrap .toolbar-wrap .toolbar-list .full-screen-btn-wrap {
  float: right;
  margin-left: 0; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .item-icon {
  font-size: 20px;
  display: inline-block;
  width: 100%;
  height: 38px;
  border-radius: 5px;
  box-sizing: border-box;
  border: solid 1px #cdcdcd; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .full-screen-btn {
  border: none; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .child-toolbar-list:before {
  content: ".";
  width: 0;
  height: 0;
  clear: both;
  visibility: hidden; }

.pad-wrap.vertical .toolbar-wrap .toolbar-list .toolbar-item .child-toolbar-list:before {
  display: none; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .child-toolbar-list {
  position: absolute;
  z-index: 100;
  margin-top: 3px;
  box-shadow: 0 0 2px 1px #cdcdcd;
  background: #fff;
  padding: 5px 2px;
  display: none; }

.vertical .toolbar-wrap .toolbar-list .toolbar-item .child-toolbar-list {
  top: 0;
  left: 40px;
  margin-top: 0;
  margin-left: 3px; }

.pad-wrap .toolbar-wrap .toolbar-list .selected-item .child-toolbar-list {
  display: block; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .item-icon:hover {
  background: #cdcdcd; }

.pad-wrap.disabled .toolbar-wrap .toolbar-list .toolbar-item .item-icon:hover {
  background: none; }

.pad-wrap.disabled .toolbar-wrap .toolbar-list .toolbar-item .item-icon {
  color: #cecece; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item .item-icon[active] {
  background: #cdcdcd;
  color: #fff; }

.pad-wrap.disabled .toolbar-wrap .toolbar-list .toolbar-item .item-icon[active] {
  background: none;
  color: #cecece; }

.disabled .split-page-wrap {
  color: #cdcdcd; }

.disabled .split-page-wrap a {
  color: #cdcdcd; }

.disabled .split-page-wrap a:hover {
  text-decoration: none;
  cursor: default; }

.pad-wrap .toolbar-wrap .toolbar-list .toolbar-item:first-child {
  margin-left: 0; }

.pad-wrap .can-wrap-outer {
  position: absolute;
  height: calc(24%);
  height: calc(100% - 76px);
  top: 38px;
  width: 100%;
  overflow: hidden; }

.pad-wrap .can-wrap-outer .can-wrap {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden; }

.pad-wrap .can-wrap canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1; }

.pad-wrap .can-wrap canvas.buffer-can-1 {
  z-index: 9; }

.pad-wrap .can-wrap canvas.buffer-can-2 {
  z-index: 8; }

.pad-wrap .can-wrap canvas.buffer-can-3 {
  z-index: 0; }

.pad-wrap .can-wrap canvas.main-can {
  z-index: 2; }

.pad-wrap .tool-input {
  visibility: hidden;
  position: absolute;
  z-index: 0;
  width: 1px;
  border: 0;
  padding: 0;
  outline: 0;
  margin: 0;
  letter-spacing: 1.46px;
  background: transparent; }

.pad-wrap .pad-tab-wrap {
  height: 38px;
  position: absolute;
  bottom: 0;
  width: 100%; }

.pad-wrap .pad-tab-wrap .pad-tab-list {
  width: 100%;
  height: 100%; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item {
  height: 100%;
  padding: 0 5px;
  display: inline-block;
  line-height: 40px;
  background: #ececec;
  cursor: pointer;
  float: left;
  position: relative;
  margin-left: 2px;
  width: 100px;
  overflow: hidden;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item.active {
  background: #cdcdcd; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item:first-child {
  margin-left: 0; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item .icon-close {
  position: absolute;
  top: 2px;
  line-height: normal;
  right: 2px;
  visibility: hidden; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item:hover .icon-close {
  visibility: visible; }

.pad-wrap .pad-tab-wrap .pad-tab-list .pad-tab-item .icon-close:hover {
  color: red; }

.pad-wrap .tool-img {
  position: absolute;
  z-index: -1;
  visibility: hidden; }

.pad-wrap .scroll-y-wrap {
  position: absolute;
  right: 0;
  height: 100%;
  width: 10px;
  top: 0;
  z-index: 20; }

.pad-wrap .scroll-x-wrap {
  position: absolute;
  bottom: 0;
  left: 0;
  width: calc(80%);
  width: calc(100% - 10px);
  height: 10px;
  z-index: 20; }

.pad-wrap .scroll-toolbar {
  position: absolute;
  left: 0;
  top: 0;
  cursor: pointer;
  width: 100%;
  border-radius: 3px;
  box-shadow: 0 0 5px #666 inset; }

.pad-wrap .scroll-x-wrap .scroll-x {
  width: 200px;
  height: 100%; }

.pad-wrap .scroll-y-wrap .scroll-y {
  width: 100%;
  height: 200px; }

/* no toolbar */
.no-toolbar .toolbar-wrap {
  display: none !important; }

.no-toolbar .can-wrap-outer {
  top: 0;
  height: calc(62%);
  height: calc(100% - 38px); }

.no-toolbar.vertical .can-wrap-outer,
.no-toolbar.vertical .pad-tab-wrap {
  width: 100% !important;
  left: 0; }

/* no toolbar */
/* no tab */
.no-tab .pad-tab-wrap {
  display: none; }

.no-tab .can-wrap-outer {
  top: 0;
  height: calc(62%);
  height: calc(100% - 38px); }

.no-tab.vertical .can-wrap-outer {
  height: 100% !important; }

/* no tab */
/* no toolbar , no tab */
.no-tab.no-toolbar .can-wrap-outer {
  height: 100%;
  width: 100%; }

